nav {
  background-color: rgb(255, 143, 111);
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

a {
  text-decoration: none;
  color: white;
}
a:hover {
  text-decoration: underline;
  color: white;
}

.navbar-brand {
  font-size: 35px;
}

.nav-item {
  font-size: 25px;
}

.jumbotron {
  background-image: url(/static/media/books-bookstore-book-reading-159711.b67485c5.jpg);
  background-position: center;
  box-shadow: inset 0px 0px 10px rgba(0, 0, 0, 0.9);
}

h1 {
  text-shadow: 2px 2px 4px #000000;
  font-size: 3em;
  letter-spacing: 4px;
}

.img-fluid {
  flex: 9 1;
  /* padding: 1px; */
  box-shadow: 0 10px 18px rgba(0, 0, 0, 0.25), 0 8px 8px rgba(0, 0, 0, 0.22);
  margin: 15px;
}

.card {
  -webkit-transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

.card:hover,
.card:focus,
.card:hover,
.card:focus {
  box-shadow: 0 18px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  flex: 5 1;
}

p.card-text {
  opacity: 80%;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

